@import url(https://use.fontawesome.com/releases/v5.5.0/css/all.css);
@import url(https://unpkg.com/simplebar@latest/dist/simplebar.css);
@import url(https://fonts.googleapis.com/earlyaccess/notosanstc.css);
@import url(https://use.fontawesome.com/releases/v5.5.0/css/all.css);
@import url(https://unpkg.com/simplebar@latest/dist/simplebar.css);
@import url(https://fonts.googleapis.com/earlyaccess/notosanstc.css);
@import url(https://npmcdn.com/tootik@1.0.2/css/tootik.min.css);
body {
    margin: 0;
    font-family: "Noto Sans TC", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body {
    background-color: #fafafa;
}

a {
    text-decoration: none;
}

.icon {
    text-align: center;

    width: 1.5em;
    padding: 0.1em;
}

html,
body {
    position: absolute;
    font-family: "Noto Sans TC", sans-serif;

    top: 0;
    left: 0;
    margin: 0;
    min-height: 100%;
    width: 100%;
}

main {
    min-height: 100vh;
}

a:visited,
a:link {
    color: inherit;
}

/* Home-nav */
#home-nav {
    position: fixed;
    display: block;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    background-color: rgba(255, 255, 255, 0.7);

    top: 0;
    left: 0.7em;
    height: 2.4em;
    line-height: 2.4em;
    font-size: 1.3em;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    z-index: 5;
}

#home-nav a {
    text-decoration: none;
    display: block;

    margin: 0;
}

#home-nav a:hover {
    color: lightseagreen;

    transition: 0.2s;
}

#home-nav .btn:hover {
    color: lightseagreen;
    transition: 0.2s;
}

#home-nav ul {
    list-style-type: none;

    margin: 0;
    padding: 0;
    height: inherit;
}

#home-nav li {
    float: left;
    text-align: center;
    overflow: hidden;

    width: 2.4em;
    height: inherit;
    transition: 0.35s;
}

#home-nav .toolbar {
    visibility: hidden;

    width: 0;
    transition: 0.25s;
}

#home-nav .active {
    background: #aaa9;
}

#music-player {
    position: absolute;
    visibility: hidden;
    transform: translateX(-350px);

    z-index: 3;
    opacity: 0.5;
    margin-top: 0.1em;
    height: 80px;
    transition: 0.35s;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

#music-player:hover {
    opacity: 1;
}

#music-player.show {
    visibility: visible;
    transform: translateX(0px);
}

/* Side-nav */
#side-nav {
    background-color: #18a5a5;
    position: fixed;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;

    left: -250px;
    top: 0;
    width: 250px;
    height: 100%;
    font-size: 1.3em;
    transition: transform 0.15s;
    transition-delay: 0.1s;
    z-index: 10;
}

.show-sidenav {
    transform: translate3d(250px, 0, 0);
}

#side-nav a {
    display: inline-block;
    position: relative;
    text-decoration: none;
    color: white;
    text-align: left;
    box-sizing: border-box;

    left: 0;
    width: 100%;
    height: 40px;
    padding: 0 0.5em;
    line-height: 40px;
}

#side-nav .sidenav-btn-wrapper {
    position: absolute;
    text-align: center;

    left: 0.7em;
    z-index: 5;
    width: 50px;
    height: 50px;
    color: white;
    line-height: 50px;
}

#side-nav a:hover {
    background-color: #89c9c96c;
    box-shadow: 0 0 28px rgba(0, 0, 0, 0.25), 0 0 10px rgba(0, 0, 0, 0.22);
    transition: 0.1s;
}

#side-nav .icon {
    margin-right: 0.2em;
}

#side-nav ul {
    list-style-type: none;

    margin: 0;
    padding: 10px;
}

#side-nav .category {
    overflow: auto;

    width: 100%;
    height: calc(100% - 215px);
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

#side-nav .profile {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-size: cover;
    background: #2ca8c7;
    background-image: radial-gradient(circle, #5fd4f1, #1c9bbb);

    height: 215px;
    width: 100%;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

#side-nav .profile img {
    display: block;
    position: relative;

    width: 45%;
    border-radius: 50%;
}

#side-nav .profile span {
    text-align: center;
    position: relative;
    display: block;
    -webkit-user-select: text;
        -ms-user-select: text;
            user-select: text;
    color: white;
}

#side-nav #profile-img:hover {
    animation: heartbeat 0.5s forwards;
}

#side-nav #profile-quote {
    font-size: 13px;
}

#side-nav #profile-name {
    font-size: 30px;
}

/* Footer */
#footer {
    position: absolute;
    background: #45454545;
    text-align: center;
    color: grey;

    width: 100%;
    padding: 1em 0;
    transition: 0.3s;
}

#footer:hover {
    background: #454545;
}

/* Others */
.btn {
    cursor: pointer;
}

.ripple {
    position: relative;
}

/* Animation effect */
.spin-effect {
    animation: spin 0.5s ease-out forwards;
}

.ripple-effect {
    position: absolute;
    overflow: hidden;

    top: -5px;
    left: -7px;
    width: 32px;
    height: 32px;
    opacity: 1;
    border-radius: 50%;
    transform: scale(0);
    background: rgba(0, 0, 0, 0.15);
    animation: ripple 0.15s ease-out forwards;
}

@media only screen and (max-width: 700px) {
    #home-nav a,
    #home-nav li {
        display: none;
    }

    #footer {
        font-size: 0.7em;
    }
}

/* Animation */
@keyframes ripple {
    100% {
        transform: scale(1.3);
    }
}

@keyframes heartbeat {
    50% {
        transform: scale(0.7);
    }
    75% {
        transform: scale(1.1);
    }
    0%,
    100% {
        transform: scale(1);
    }
}

@keyframes stretch {
    from {
        width: 0;
    }
    to {
        width: 200px;
    }
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.section {
    padding: 0.6em 2.5em;
}

.section > h2 {
    margin: 0.3em 0;
}

.focus-light {
    background: #37506d33;
}

.focus {
    background: #555;
    color: white;
}

/* Header */
#profile {
    background: #eeee;
}

#profile > .header {
    display: flex;
    align-items: center;
    justify-content: center;

    margin: 1.5em 0;
}

#profile .profile-img {
    display: flex;
}

#profile img {
    width: 17em;
    height: 17em;
    border-radius: 50%;
}

#profile .title {
    color: #333;
    font-size: 0.85em;
    margin-bottom: 0.15em;
}

#profile .title a:hover {
    color: #3fc1c9;
}

#profile .basic-info {
    display: flex;
    justify-content: center;

    width: 40vw;
}

#profile .title-box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
}

#profile .name {
    padding: 0.3em;
    font-size: 1.8em;
}

#profile .name :nth-child(1) {
    font-size: 1.7em;
}

#profile .info a {
    display: inline-block;
    text-decoration: none;

    margin: 0.3em;
    font-size: 2.2em;
    transition: 0.15s ease;
}

#profile .info a:hover {
    transform: scale(1.5);
}

/* Introduction */
#intro {
    display: flex;
}

#intro .bio {
    width: 55vw;
    margin-left: 1.5em;
    padding-left: 1.5em;
    border-left: solid #cccc;
}

#intro .bio .info {
    display: flex;
    flex-direction: column;

    font-size: 0.9em;
}

#intro .bio a {
    text-decoration: none;
}

#intro .bio a:hover {
    color: #3fc1c9;
}

#intro .bio .paragraph {
    padding: 1em 0;
}

/* Education */
#intro .education {
    width: 40vw;
}

#intro .timeline-container {
    display: flex;
    overflow: hidden;
    box-sizing: border-box;
    justify-content: center;

    min-height: 15em;
}

#intro .edu-timeline {
    position: absolute;

    width: 35vw;
    max-width: 25em;
}

#intro .edu-timeline::after {
    content: "";
    position: absolute;
    box-sizing: border-box;
    background-color: white;

    top: 0;
    bottom: 0;
    width: 0.6em;
    margin-left: -0.55em;
    border: solid 1px #cccc;
    border-radius: 0.2em;
}

#intro .edu-timeline .container {
    position: relative;

    padding: 0 0 1em 1.5em;
}

#intro .edu-timeline .container::after {
    content: "";
    position: absolute;
    background-color: white;

    width: 1.6em;
    height: 1.6em;
    z-index: 1;
    border-radius: 50%;
    top: 0;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

#intro .edu-timeline .before::after {
    /* 1.1 = circle_radius(1.6 / 2) + line_width_half(0.6 / 2)  */
    left: -1.1em;
}

#intro .edu-timeline .current::after {
    /* 1.3 = circle_radius(1.6 / 2) + border_size(0.2) + line_width_half(0.6 / 2)  */
    left: -1.3em;
    border: 0.2em solid #ff5e5f;
}

#intro .edu-timeline .content {
    position: relative;
    text-align: center;
    background-color: white;

    padding: 0.1em;
    border-radius: 0.5em;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

#intro .edu-timeline .school {
    display: flex;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    align-items: center;
    justify-content: center;

    color: #1b262c;
    font-size: 1.2em;
    transition: 0.1s;
}

#intro .edu-timeline .school > img {
    height: 0;
    width: auto;
    margin: 0.2em 0.2em 0.2em 0;
}

#intro .edu-timeline .major {
    display: block;
    color: gray;
    display: none;

    font-size: 0.9em;
    font-weight: 395;
}

#intro .edu-timeline .school:hover + .major {
    display: block !important;
}

#intro .edu-timeline .school:hover {
    color: black !important;

    font-size: 1.3em;
    cursor: default;
}

#intro .edu-timeline .school:hover img {
    height: 1.25em;
}

/* Experiences */
#exp > .content {
    display: flex;
    box-sizing: border-box;

    height: 17em;
}

#exp .item {
    display: flex;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    align-items: center;

    padding: 0.5em 0.5em;
    margin: 0.3em 1em;
    border-radius: 0.5em;
}

#exp .item img {
    height: auto;
    width: 2.5em;
    margin-right: 0.6em;
}

#exp .title-list {
    width: 45vw;
    height: inherit;
    margin-right: 1em;
    overflow: hidden;
}

#exp .title div:nth-child(1) {
    font-size: 1.1em;
}

#exp .title div:nth-child(2) {
    font-size: 0.9em;
    font-weight: 395;
}

#exp .info {
    box-sizing: border-box;
    background: white;

    width: 55vw;
    padding: 1em;
    height: inherit;
    border-radius: 0.5em;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 2px, rgba(0, 0, 0, 0.2) 0px 1px 2px;
}

#exp .period {
    font-size: 1.05em;
}

#exp .supervisor {
    font-size: 0.9em;
    margin: 0.1em 0;
}

#exp .description {
    margin: 0.5em 0;
}

/* My Skills */
#skills > .content {
    display: flex;
}

#skills .details {
    background: white;
    box-sizing: border-box;

    height: 15em;
    width: 50vw;
    padding: 1em 1.5em;
    margin-right: 1em;
    border-radius: 0.5em;
    border: 1px solid;
    border-color: transparent;
    transition: 0.1s;
}

#skills .details:hover {
    border-color: #ccc;
}

#skills ul {
    display: flex;
    list-style: none;
    flex-direction: column;
    justify-content: space-between;

    margin: 0;
    padding: 1em;
    width: 50vw;
}

#skills li {
    display: flex;
    align-items: center;

    font-size: 1.1em;
    margin: 0.1em 0;
}

#skills li .logo-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;

    width: 2em;
    margin-right: 0.2em;
}

#skills li img {
    height: 1em;
    width: auto;
}

#skills li span {
    font-weight: 500;
    width: 10em;
}

#skills .progress-bar {
    width: 100%;
    height: 1em;
    margin: 0 1em;
    border: 1px solid #ccc;
    border-radius: 0.3em;
}

#skills .progress-bar-fill {
    display: block;
    background-color: #3fc1c9dd;

    height: 100%;
    border-radius: inherit;
    transform-origin: 0;
    transition: 2s ease-in-out;
}

/* Projects */
#projects .content {
    background: white;
    border-radius: 0.3em;
    border: 0.05em solid #ccc;
}

#projects .filter-bar {
    border-bottom: 0.05em solid #ccc;
}

#projects .filter-bar ul {
    list-style-type: none;
    overflow: hidden;
    background: #eee;

    margin: 0;
    padding: 0;
    width: 100%;
    border-radius: 0.2em;
}

#projects .filter-bar li {
    float: left;
    cursor: pointer;
    text-align: center;

    width: 5em;
    padding: 0.3em;
}

#projects .filter-bar li:hover {
    background: #555;
    color: white;
}

#projects .filter-bar .active {
    background: #555;
    color: white;
}

#projects .projects-list {
    display: flex;
    flex-direction: row;
}

#projects .list {
    overflow: auto;

    width: 35%;
    min-width: 35%;
    box-sizing: border-box;
}

#projects .list ul {
    list-style-type: none;

    margin: 0;
    padding: 0.5em;
    max-height: 50vh;
}

#projects .list li {
    cursor: pointer;

    padding: 0.3em;
    margin: 0.1em;
    border-radius: 0.2em;
}

#projects .list li:hover {
    background: #777;
    color: white;
}

#projects .info {
    background: white;

    width: 100%;
    padding: 1em;
    height: 20em;
    border-left: 0.08em solid #ccc;
}

#projects .info h3 {
    margin: 0;
}

#projects .links {
    color: #333;
    font-size: 0.9em;
    margin-bottom: 0.5em;
}

#projects .links a {
    margin-right: 0.3em;
}

#projects .links a:hover {
    color: #3fc1c9;
}

#projects .tags {
    border-left: 0.1em solid #aaa;
}

#projects .tags span {
    cursor: pointer;

    border-radius: 0.2em;
    padding: 0.1em 0.2em;
    margin-left: 0.25em;
}

#projects .tags span:hover {
    background: #eee;
}

#projects .info img {
    max-width: 100%;
    max-height: 35vh;
    margin: 0.1em 0;
}

/* My Activities */
#activities > .content .item {
    cursor: pointer;

    padding: 0.6em;
    border-radius: 0.5em;
}

#activities > .content .item:hover {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19) !important;
}

#activities > .content .title {
    font-size: 1.1em;
}

#activities > .content img {
    display: none;

    width: auto;
    height: 20em;
    margin-top: 1em;
    border-radius: 0.3em;
}

#activities > .content .info {
    display: flex;
    justify-content: space-between;
    align-items: center;

    font-size: 0.9em;
}

/* My Publications */
#publications > .content {
    display: flex;
    flex-direction: column;
}

#publications > .content a {
    text-decoration: none;

    padding: 0.1em 0.6em;
}

#publications > .content a:hover {
    background: #0001;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 2px, rgba(0, 0, 0, 0.2) 0px 1px 2px;
}

@media only screen and (max-width: 900px) {
    /* header */
    h2.header {
        text-align: center;
    }

    /* profile */
    #profile > .header {
        flex-wrap: wrap;
    }
    #profile .profile-img {
        width: 100vw;
        justify-content: center;
    }
    #profile .basic-info {
        width: 100vw;
        justify-content: center;
    }

    /* Introduction */
    #intro {
        flex-wrap: wrap;
    }

    #intro .education {
        width: 100vw;
    }

    #intro .timeline-container .edu-timeline {
        width: 70vw;
    }

    #intro .bio {
        text-align: center;

        margin: 0;
        padding: 0;
        border: none;
        width: 100vw;
    }

    /* Skills */
    #skills > .content {
        flex-wrap: wrap-reverse;
    }

    #skills .details {
        width: 100%;
    }

    #skills ul {
        width: 100vw;
    }

    /* Activities */
    #activities img {
        height: auto;
        max-width: 90%;
        max-height: 20em;
    }
}

@media only screen and (max-width: 450px) {
    #intro .edu-timeline {
        font-size: 0.85em;
    }
}

